

































import Vue from "vue";

import Item from "../interfaces/Item";

export default Vue.extend({
    props: {
        item: {
            type: Object as () => Item,
            required: true
        }
    },
    computed: {
        iconUrl: function () {
            console.log("computed stub " + this.item.iconFileName);
            return "/game_assets/" + this.item.iconFileName;
        }
    }
});
